import React, { Component } from 'react';
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';
import data from '../data/portfolio.json';
import ShowMoreText from 'react-show-more-text';


class Portfolio extends Component {
  
  constructor(props) {
    super(props);
    this.state = {
      photoIndex: 0,
      isOpen: false
    };
  }


  render() {
    const { photoIndex, isOpen } = this.state;
    return (
      <div className="section"> 
          <h5>Portfolio</h5>
          <nav>
            <div className="nav nav-tabs">
              <a className="nav-item nav-link active"  data-toggle="tab" href="#website" >Website</a>
              <a className="nav-item nav-link" id="nav-profile-tab" data-toggle="tab" href="#graphic">Graphic Design</a>
            </div>
          </nav>
          <div className="tab-content mt-3">
            <div className="tab-pane fade  show active" id="website">
              {data.map((project, index) => (
                <div key={index}>
                    {project.website.map(({title, image, content, url}, index) => ( 
                      <div  key={index}>
                        <div className="card mb-3"  >
                        {image ? <img src={require('../../images/' + image)} className="img-about img-pointer" alt={title}  onClick={() => this.setState({ isOpen: true, photoIndex: require('../../images/' + image) })} /> : ''}
                          <div className="card-body">
                            <h6>{ title } {url ? <a href={url} rel="noopener noreferrer" target="_blank"> <span className="fa fa-external-link-alt link-icon"> </span> </a> : ''} </h6>
                            <ShowMoreText
                              lines={3}
                              more='[view more]'
                              less='[view less]'
                              anchorClass='view-more '
                              onClick={this.executeOnClick}
                              expanded={false}
                              >
                              <p className="card-text" dangerouslySetInnerHTML={ {__html: content} } />
                            </ShowMoreText>
                            
                          </div>
                        </div>
                      </div>
                    ))}
                </div>
              ))}

            </div>
            
            <div className="tab-pane fade" id="graphic" >
              {data.map((project, index) => (
                <div key={index}>
                    {project.graphic.map(({title, image, content}, index) => ( 
                      <div  key={index}>
                        <div className="card mb-3"  >
                          {image ? <img src={require('../../images/' + image)} className="img-about img-pointer" alt={title}  onClick={() => this.setState({ isOpen: true, photoIndex: require('../../images/' + image) })} /> : ''}
                          <div className="card-body">
                            <h6>{ title }</h6>
                            <ShowMoreText
                              lines={3}
                              more='[view more]'
                              less='[view less]'
                              anchorClass='view-more '
                              onClick={this.executeOnClick}
                              expanded={false}
                              >
                              <p className="card-text" dangerouslySetInnerHTML={ {__html: content} } />
                            </ShowMoreText>

                          </div>
                        </div>
                      </div>
                    ))}
                </div>
              ))}
            </div>
          </div>

          {isOpen && (
            <Lightbox
            mainSrc={photoIndex}
            onCloseRequest={() => this.setState({ isOpen: false })}
            />
          )}
          

      </div>
    );
  }
}

export default Portfolio;
