import React, { Component } from 'react';
import data from '../data/social.json';



const profileImage = 'King Bon Profile Picture.jpg';

class Sidebar extends Component {
  
  render() {
    return (
      <div className="sidebar-item">
        <div className="card-menu ">
          <div className="card-body text-lg-left text-center pt-0 ">
            <div className="text-center">
              <img src={require('../../images/' + profileImage)} className="rounded-circle" alt="..." />
            </div>
            <h4 className="card-title mt-4 _1d2129">King Bon Racimo</h4>
            <p className="card-text _606770">Web Developer, Graphic Designer, Computer Savvy, Lorem ipsum dolor</p>
          
            <div className="button">
              <a href="tel:+639057941884" className="btn btn-success btn-sm mr-3 mb-3" role="button" aria-pressed="true">Call now</a> 
              <a href="https://www.facebook.com/messages/t/phl.kingbon" target="_blank" rel="noopener noreferrer"  className="btn btn-primary btn-sm mb-3" role="button" aria-pressed="true">Message</a>
            </div>
          
            <ul className="list-inline social">
              <div>
                {data.map(( {label, link, icon} , index) => (
                  <li key={index} className="list-inline-item">
                    <a  target="_blank" rel="noopener noreferrer" href={ link } className={ icon  } >
                      <span className="label">{ label }</span>
                    </a>
                  </li>
                ))}
              </div>
            </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default Sidebar;
